.card{
    display: flex;
    justify-content: center;
    gap: 2rem;
}
.card__content{
    width: 20%;
    margin: auto 0;
}
.card__icon{
    font-size: 70px;
    margin: auto 0;
}

/* =================== Media Queries (Small Devices)======================= */
@media screen and (max-width: 600px){
    
    .card__content{
        width: 30%;
    }
    .card__icon{
        font-size: 50px;
    }
}