.services__nav {
    display: flex;
    border: 2px solid var(--color-primary);
}

.services__nav__link {
    width: 100%;
    text-align: center;
    padding: 0.7rem 0;
    font-size: 1.2rem;
    
}

.services__nav__link.inactive {
    color: var(--color-black);
    transition: var(--transition);
}
.services__nav__link.inactive:hover{
    color: var(--color-white);
    background-color: var(--color-black);
}


.services__nav__link.active {
    background-color: var(--color-black);
    color: var(--color-white);
}

