.objective{
    height: 87vh;
    background-image: url("../../../../Assets/headline-background.png");
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.objective__main{

    width: 60%;
    margin: auto;
}

.objective__text{
    font-size: 1.8rem;
    font-weight: 600;
    padding-top: 6rem;
    padding-bottom: 5rem;
    height: 17rem;
}

.objective__btns{
    display: flex;
    gap: 2rem;
    flex-direction: column;
}
.objective__btns .btn{
    margin: auto;
    width: 12rem;
}

.objective__btns .btn{
    font-size: 1.2rem;
    font-weight:500;
}

.objective__user-name{
    font-size: 1.3rem;
}





/* =================== Media Queries (Medium Devices)======================= */
@media screen and (max-width: 1024px){
    .objective__text{
        font-size: 1.5rem;
        font-weight: 600;
    }
}

/* =================== Media Queries (Small Devices)======================= */
@media screen and (max-width: 600px){
    
    .objective__text{
        height: 20rem;
        font-size: 1.2rem;
        font-weight: 500;
    }
    .objective__btns .btn{
        width: 8rem;
    }
    .objective__btns .btn{
        font-size: 0.8rem;
    }

}