.about{
    margin-top: 3rem;
    display: flex;
    gap: 3rem;
    justify-content: center;
}
.about__image{
    margin-top: 2rem;
    width: 25%
}
.about__content h2{
    margin-bottom: 2rem;
}
.about__content p{
    font-size: 0.9rem;
}
.about__content{
    width: 60%;
}

/* =================== Media Queries (Medium Devices)======================= */
@media screen and (max-width: 1024px){
    
}

/* =================== Media Queries (Small Devices)======================= */
@media screen and (max-width: 600px){
    
    .about__image{
        display:none;
    }
    .about__content h2{
        font-size: 1rem;
    }
    .about__content p{
        font-size: 0.7rem;
    }
    .about__content{
        width:90%;
    }
}