.tiffin-service__top-content img{

    height: 300px;
    opacity: 0.4;
}

.tiffin-service__top-content{
    position: relative;
}

.tiffin-service__top-content h1{
    position: absolute;
    bottom: 2rem;
    left: 2rem;
}

.tiffin-service__main-content{

    margin-left: 2rem;
}
.tiffin-service__main-content h2{

    margin-top: 4rem;
}

.tiffin-service__main-content h2{

    border-bottom: 2px solid var(--color-black);
    width: 30%
}

.tiffin-service__main-content__photos .photos-swiper{

    margin-top: 3rem;
    width: 80%;
    height: 400px;
}

.tiffin-service__main-content__photos .photos-swiper img{
    width: 80%;
    margin: auto ;
}

.swiper-button-next:after, .swiper-button-prev:after{
    color: var(--color-black);
}

.menu-image{
    margin-top: 3rem;
    cursor: pointer;
}
.menu-image img{

    width: 30%;
    margin: 0 auto;
    border: 2px solid var(--color-black);
}

.full-size-image{

    display: none;
    position: absolute;
    background-color:rgba(0, 0, 0, 0.635);
    top: 600px;
    left: 0;
    right: 0;
    z-index: 10;
    
}

.image-area img{

    width: 70%;
    margin: auto;
    margin-top: 50%;
    transform: translateY(-50%);
}
.cross-area{

    text-align: right;
    color: var(--color-white);
    font-size: 2rem;
    padding: 1rem 2rem;
}
.cross-area span{

    cursor: pointer;
}

.tiffin-service__contact .contact-details{

    text-align: left;
    margin-top: 2rem;
    margin-right: 1rem;
}

.contact-block{
    margin-bottom: 20px;
}

.contact-block__icon{

    margin-right: 5px;
}

/* =================== Media Queries (Medium Devices)======================= */
@media screen and (max-width: 1024px){
    .tiffin-service__main-content__photos .photos-swiper{
        height: 300px;
    }
}

/* =================== Media Queries (Small Devices)======================= */
@media screen and (max-width: 600px){
    .tiffin-service__main-content__photos .photos-swiper{
        height: 200px;
        width: 100%;
    }
    .swiper-button-prev{

        font-size: 1rem;
    }

    .swiper-button-next:after, .swiper-button-prev:after{
        font-size: 1.5rem !important;
    }

    .tiffin-service__main-content__photos .photos-swiper img{
        width: 75%;
        margin-top: 30px;
    }
    .image-area img{
        width: 90%;
        
    }
    .full-size-image{
        top:800px;
        
    }
    .cross-area{

        text-align: right;
        color: var(--color-white);
        font-size: 2rem;
        padding: 1rem 2rem;
    }

    .menu-image img{
        width: 50%;
    }
}