.tiffin-card__content__des{
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
}

.tiffin-card__content__des .category{

    font-size: 0.8rem;
}

.tiffin-card__image img{

    border-radius: 15px;
}

/*=================== Media Queries (Small Devices)=======================*/
@media screen and (max-width: 600px){
    .tiffin-card__content__des{

        flex-direction: column;
    }
    .tiffin-card__content__des .price{
        margin-top: 10px;
    }
}